import User from '../data/User';
import { $, getInnerInputValues } from '../utils';
import { updateUserInfo } from '../utils/auth';
import { validateUserInfo } from '../validator';
import { showSnackBar } from '../utils/index';

const pageTemplate = ({ email, name }) => `
  <section class="user-information-form-section">
      <form id="update-my-info-form" >
          <label>Ïù¥Î©îÏùº<br>
              <input type="email" name="email" value=${email} disabled>
          </label>
          <label>Ïù¥Î¶Ñ<br>
              <input type="text" name="name" value=${name} placeholder="Ïù¥Î¶ÑÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî">
          </label>
          <label>ÎπÑÎ∞ÄÎ≤àÌò∏<br>
              <p class="input-guide">ÎåÄÎ¨∏Ïûê ÏïåÌååÎ≤≥, ÏÜåÎ¨∏Ïûê ÏïåÌååÎ≤≥, Ïà´ÏûêÎ•º Í∞ÅÍ∞Å 1Ïûê Ïù¥ÏÉÅ Ìè¨Ìï®ÌïòÎäî Ï†ÑÏ≤¥ 8Ïûê Ïù¥ÏÉÅÏùò ÎπÑÎ∞ÄÎ≤àÌò∏Î•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî.</p>
              <input type="password" name="password" placeholder="ÎπÑÎ∞ÄÎ≤àÌò∏Î•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî">
          </label>
          <label>ÎπÑÎ∞ÄÎ≤àÌò∏ ÌôïÏù∏<br>
              <input type="password" name="passwordConfirm" placeholder="ÎπÑÎ∞ÄÎ≤àÌò∏Î•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî">
          </label>
          <button class="button accent">ÌôïÏù∏</button>
      </form>
  </section>
`;

class UpdateMyInfoPageView {
  loadPage = () => {
    $('.main').innerHTML = pageTemplate({
      email: User.email,
      name: User.name,
    });
    $('#update-my-info-form').addEventListener('submit', this.onSubmitUpdateMyInfoForm);
  };

  onSubmitUpdateMyInfoForm = (event) => {
    event.preventDefault();
    const { email, name, password, passwordConfirm } = getInnerInputValues(event.target);
    try {
      validateUserInfo({ email, name, password, passwordConfirm });
    } catch (err) {
      showSnackBar(err.message);
      return;
    }
    updateUserInfo({ email, name, password });
  };
}

export default new UpdateMyInfoPageView();
