import User from '../data/User';
import { IUser } from '../interface';
import { loadMainPage } from '../routes';
import { showSnackBar } from './index';

interface SignInfo {
  email: string;
  password: string;
  name? :string;
}

interface AuthInfo {
  accessToken: string,
  id: number
  expiration: number,
}

const devURL = 'http://localhost:3000/';
const deployURL = 'https://soyi47-auth-server.herokuapp.com/';

const currentAuthServer = deployURL;

const signUpURL = `${currentAuthServer}signup/`;
const loginURL = `${currentAuthServer}login/`;
const userInfoURL = (id) => `${currentAuthServer}600/users/${id}`;

function signUp(signUpInfo: SignInfo) {
  fetch(signUpURL, {
    method: 'POST',
    body: JSON.stringify(signUpInfo),
    headers: {
      'Content-Type': 'application/json',
    },
  }).then(async res => {
    if (!res.ok) {
      const message = await res.text();
      throw new Error(message.slice(1, -1));
    }
    return res.json();
  })
    .then(response => {
      const { id, email, name }: IUser = response.user;
      const userAuth: AuthInfo = {
        accessToken: response.accessToken,
        id,
        expiration: Date.now() + 1000 * 60 * 60,
      };
      localStorage.setItem('userAuth', JSON.stringify(userAuth));
      User.setUser({ id, email, name });
      loadMainPage();
    })
    .catch(err => {
      if (err.message === 'Email already exists') {
        showSnackBar('Ïù¥ÎØ∏ Í∞ÄÏûÖÌïú Ïù¥Î©îÏùºÏûÖÎãàÎã§.');
      }
    });
}

function login(loginInfo: SignInfo) {
  fetch(loginURL, {
    method: 'POST',
    body: JSON.stringify(loginInfo),
    headers: {
      'Content-Type': 'application/json',
    },
  }).then(async res => {
    if (!res.ok) {
      const message = await res.text();
      throw new Error(message.slice(1, -1));
    }
    return res.json();
  })
    .then(response => {
      const { id, email, name }: IUser = response.user;
      const userAuth: AuthInfo = {
        accessToken: response.accessToken,
        id,
        expiration: Date.now() + 1000 * 60 * 60,
      };
      localStorage.setItem('userAuth', JSON.stringify(userAuth));
      User.setUser({ id, email, name });
      loadMainPage();
    })
    .catch(err => {
      switch (err.message) {
        case 'Cannot find user':
          showSnackBar('Îì±Î°ùÎêòÏßÄ ÏïäÏùÄ Ïù¥Î©îÏùºÏûÖÎãàÎã§.');
          break;
        case 'Incorrect password':
          showSnackBar('ÏûòÎ™ªÎêú ÎπÑÎ∞ÄÎ≤àÌò∏ÏûÖÎãàÎã§.');
          break;
        default:
          break;
      }
    });
}

const logout = () => {
  localStorage.removeItem('userAuth');
  User.initialize();
  loadMainPage();
};

function requestUserInfo(userAuth) {
  const { id } = userAuth;
  const accessToken = `Bearer ${userAuth.accessToken}`;

  return fetch(userInfoURL(id), {
    method: 'GET',
    headers: {
      'Content-Type': 'application/json',
      Authorization: accessToken,
    },
  })
    .then(async res => {
      if (!res.ok) {
        const message = await res.text();
        throw new Error(message.slice(1, -1));
      }
      return res.json();
    })
    .then(response => {
      const { email, name }: Partial<IUser> = response;
      User.setUser({ id, email, name });
    })
    .catch(error => console.error('ÏóêÎü¨', error.message));
}

function updateUserInfo(newUserInfo) {
  const userAuth: AuthInfo = JSON.parse(localStorage.getItem('userAuth'));
  if (!userAuth) return;

  const { id } = userAuth;
  const accessToken = `Bearer ${userAuth.accessToken}`;

  fetch(userInfoURL(id), {
    method: 'PUT',
    body: JSON.stringify(newUserInfo),
    headers: {
      'Content-Type': 'application/json',
      Authorization: accessToken,
    },
  })
    .then(async res => {
      if (!res.ok) {
        const message = await res.text();
        throw new Error(message.slice(1, -1));
      }
      return res.json();
    })
    .then(response => {
      const { email, name }: Partial<IUser> = response;
      User.setUser({ id, email, name });
      loadMainPage();
    })
    .catch(error => console.error('ÏóêÎü¨', error.message));
}

function getSavedUserInfo(): AuthInfo | null {
  const userAuth: AuthInfo | null = JSON.parse(localStorage.getItem('userAuth'));
  if (userAuth?.expiration < Date.now()) {
    localStorage.removeItem('userAuth');
  }
  return userAuth;
}

export {
  signUp,
  login,
  logout,
  requestUserInfo,
  updateUserInfo,
  getSavedUserInfo,
};
